<template>
	<div>
		<div class="operation_header">
			<div class="operation_select">
				<el-select v-model="value" @change="changeOPtions">
			    <el-option
			      v-for="item in options"
			      :key="item.value"
			      :label="item.label"
			      :value="item.value">
			    </el-option>
			  </el-select>
			</div>
			<div>
				 <el-input v-model="searchcontent" clearable></el-input>
			</div>
			<div class="operation_picker">
				 <span class="timespan">{{$t('user.timerange')}}</span>
			   <el-date-picker
	      	v-model="date"
	      	value-format="yyyy-MM-dd HH:mm:ss"
	      	type="daterange"
	      	:range-separator="$t('user.to')"
	      	:start-placeholder="$t('user.startdate')"
	      	:end-placeholder="$t('user.enddate')">
	    	</el-date-picker>
			</div>		  
    	<div class="operation_btn">
    		<el-button size="small" type="primary" @click="search">{{$t('common.search')}}</el-button>
    	</div>    	
		</div>
		<div v-show="iflabelcode" class="operation_table">
			<table class="labeltable" v-if="Object.keys(mate_info).length">
				<caption class="caption">{{$t('storage.label_code')}}：<span>{{mate_info.label_code}}</span></caption>
				<tr class="label_tr">
          <td class="title_td_pc">{{$t('storage.customer_name')}}</td>
          <td class="content_td_pc">{{mate_info.customer}}</td>
          <td class="title_td_pc">{{$t('storage.description')}}</td>
          <td class="content_td_pc">{{mate_info.description}}</td>
        </tr>
        <tr>
          <td class="title_td_pc">{{$t('storage.supplier_name')}}</td>
          <td class="content_td_pc">{{mate_info.supplier_name}}</td>
          <td class="title_td_pc">{{$t('storage.shelf_id')}}</td>
          <td class="content_td_pc">{{mate_info.shelf_id}}</td>
        </tr>
        <tr>
          <td class="title_td_pc">{{$t('storage.part_num')}}</td>
          <td class="content_td_pc">{{mate_info.part_num}}</td>
          <td class="title_td_pc">{{$t('storage.position')}}</td>
          <td class="content_td_pc">{{mate_info.position}}</td>
        </tr>
        <tr>
          <td class="title_td_pc">{{$t('storage.mfg_date')}}</td>
          <td class="content_td_pc">{{mate_info.mfg_date}}</td>
          <td class="title_td_pc">{{$t('storage.esd')}}</td>
          <td class="content_td_pc">{{esd}}</td>
        </tr>
        <tr>
        	<td class="title_td_pc">{{$t('storage.msl')}}</td>
        	<td class="content_td_pc">{{msl}}</td>
        	<td class="title_td_pc">{{$t('storage.mate_type')}}</td>
        	<td class="content_td_pc">{{mate_info.mate_type}}</td>
        </tr>
				<tr>
        	<td class="title_td_pc">{{$t('storage.lot_code')}}</td>
        	<td class="content_td_pc">{{mate_info.lot_code}}</td>
        	<td class="title_td_pc">{{$t('storage.warehouse_name')}}</td>
        	<td class="content_td_pc">{{mate_info.warehouse_name}}</td>
        </tr>
      </table>
      <el-table
       :data="record_info"
       :empty-text="$t('common.nodata')"
				border>
				<el-table-column
					prop="in_time"
					:formatter="timeFormatter"
				  :label="$t('operation.time')">						
				</el-table-column>
				<el-table-column
					prop="op_code"
					:formatter="formatterCode"
				  :label="$t('operation.event')">						
				</el-table-column>
				<el-table-column
					prop="order_id"
				  :label="$t('operation.orderid')">						
				</el-table-column>
				<el-table-column
					prop="shelf_id"
				  :label="$t('storage.shelf_id')">						
				</el-table-column>
				<el-table-column
					prop="position"
				  :label="$t('operation.position')">						
				</el-table-column>
				<el-table-column
					prop="op_name"
				  :label="$t('operation.opperson')">						
				</el-table-column>
      </el-table>
		</div>
		<!-- 操作人 -->
		<div v-show="ifperson">
			<div class="opname"><span>{{$t('operation.opperson')}}：</span>{{opname}}</div>
			<el-table
       :data="record_info"
       :empty-text="$t('common.nodata')"
				border>
				<el-table-column
					prop="in_time"
					:formatter="timeFormatter"
				  :label="$t('operation.time')">						
				</el-table-column>
				<el-table-column
					prop="op_code"
					:formatter="formatterCode"
				  :label="$t('operation.event')">						
				</el-table-column>
				<el-table-column
					prop="order_id"
				  :label="$t('operation.orderid')">						
				</el-table-column>
				<el-table-column
					prop="shelf_id"
				  :label="$t('storage.shelf_id')">						
				</el-table-column>
				<el-table-column
					prop="position"
				  :label="$t('operation.position')">						
				</el-table-column>
				<!-- <el-table-column
					prop="op_name"
				  :label="$t('operation.opperson')">						
				</el-table-column> -->
			</el-table>
		</div>
		<!-- 钢网架 -->
		<div v-show="ifshelf">
			<div class="opname"><span>{{$t('storage.shelf_id')}}：</span>{{shelfid}}</div>
			<el-table
       :data="record_info"
       :empty-text="$t('common.nodata')"
				border>
				<el-table-column
					prop="in_time"
					:formatter="timeFormatter"
				  :label="$t('operation.time')">						
				</el-table-column>
				<el-table-column
					prop="op_code"
					:formatter="formatterCode"
				  :label="$t('operation.event')">						
				</el-table-column>
				<el-table-column
					prop="order_id"
				  :label="$t('operation.orderid')">						
				</el-table-column>
				<!-- <el-table-column
					prop="shelf_id"
				  :label="$t('storage.shelf_id')">						
				</el-table-column> -->
				<el-table-column
					prop="position"
				  :label="$t('operation.position')">						
				</el-table-column>
				<el-table-column
					prop="op_name"
				  :label="$t('operation.opperson')">						
				</el-table-column>
			</el-table>
		</div>
		<!-- 唯一单据编号 -->
		<!-- <div v-show="false">
			<el-tabs type="border-card">
			  <el-tab-pane label="工单发料记录">
			  	<table class="labeltable">
					<caption class="caption">唯一依据单号：<span>{{order_id}}</span></caption>
					<tr class="label_tr">
	          <td class="order_td">发料时间</td>
	          <td class="order_content_td">{{mate_info.customer_name}}</td>
	          <td class="order_td">发料人</td>
	          <td class="order_content_td">{{mate_info.description}}</td>
	          <td class="order_td">目的地</td>
	          <td class="order_content_td">{{mate_info.supplier_name}}</td>
	          <td class="order_td">发料指令</td>
	          <td class="order_content_td">{{mate_info.shelf_id}}</td>
	        </tr>
	      </table>
	      <el-table
	       :data="order_record"
	       :empty-text="$t('common.nodata')"
					border>
					<el-table-column
						prop="in_time"
						:formatter="timeFormatter"
					  label="物料编号">						
					</el-table-column>
					<el-table-column
						prop="op_code"
						:formatter="formatterCode"
					  label="应发数量">						
					</el-table-column>
					<el-table-column
						prop="order_id"
					  label="实发数量">						
					</el-table-column>
					<el-table-column
						prop="shelf_id"
					  label="实发盘数">						
					</el-table-column>
					<el-table-column
						prop="position"
					  label="详细物料情况">						
					</el-table-column>
	      </el-table>
			  </el-tab-pane>
			  <el-tab-pane label="站为表发料记录">
			  	<table class="labeltable">
					<caption class="caption">唯一依据单号：<span>{{order_id}}</span></caption>
					<tr class="label_tr">
	          <td class="order_td">发料时间</td>
	          <td class="order_content_td">{{mate_info.customer_name}}</td>
	          <td class="order_td">发料人</td>
	          <td class="order_content_td">{{mate_info.description}}</td>
	          <td class="order_td">目的地</td>
	          <td class="order_content_td">{{mate_info.supplier_name}}</td>
	          <td class="order_td">发料指令</td>
	          <td class="order_content_td">{{mate_info.shelf_id}}</td>
	        </tr>
	      </table>
	      <el-table
	       :data="order_record"
	       :empty-text="$t('common.nodata')"
					border>
					<el-table-column
						type="index"
						width="60px"
					  label="序号">						
					</el-table-column>
					<el-table-column
						prop="op_code"
						:formatter="formatterCode"
					  label="站位描述">						
					</el-table-column>
					<el-table-column
						prop="order_id"
					  label="物料编码">						
					</el-table-column>
					<el-table-column
						prop="shelf_id"
					  label="唯一码">						
					</el-table-column>
					<el-table-column
						prop="position"
					  label="数量">						
					</el-table-column>
	      </el-table>
			  </el-tab-pane>
			</el-tabs>				
		</div> -->
	</div>
</template>
<script>
import {operationRecords } from '@/request/api/material';// 导入api接口
import { timeConvert } from '@/utils/public'
	export default{
		data(){
			return{
				value:'label_code',
				label_code:'',
				searchcontent:'',
				mate_info:'',
				record_info:[],
				// order_record:[],
				date:[],
				options:[
				{'value':'label_code','label':'唯一码'},
				{'value':'op_name','label':'操作人'},
				{'value':'shelf_id','label':'料架编号'},
				// {'value':'order_id','label':'单据编号'},
				],
				iflabelcode:false,
				ifperson:false,
				ifshelf:false,
				iforder:false,
				opname:'',
				shelfid:'',
				// order_id:''
			}
		},
		computed:{
			esd(){
				if (this.mate_info.esd) {
					return '是'
				} else {
					return '否'
				}
			},
			msl(){
				if (this.mate_info.msl) {
					return '是'
				} else {
					return '否'
				}
			}
		},
		methods:{
			changeOPtions(value){
				this.value = value
			},
			search(){
				this.mate_info = ''
				this.record_info = []
				let params = {}
				if(this.date.length){
					params.start_time = this.date[0]
					params.end_time = this.date[1]
				}else{
					params.start_time = ''
					params.end_time = ''
				}			
				if(this.value =="label_code"){
					params.label_code = this.searchcontent
					this.iflabelcode = true
					this.ifperson = false
					this.ifshelf = false
					// this.iforder = false
				}
				if(this.value == 'op_name'){
					params.op_name = this.searchcontent
					this.iflabelcode = false
					this.ifshelf = false
					// this.iforder = false
					this.ifperson = true
					this.opname = this.searchcontent
				}
				if(this.value =='shelf_id'){
					params.shelf_id = this.searchcontent
					this.iflabelcode = false
					this.ifperson = false
					// this.iforder = false
					this.ifshelf = true
					this.shelfid = this.searchcontent
				}
				// if(this.value =='order_id'){
				// 	params.order_id = this.searchcontent
				// 	this.iflabelcode = false
				// 	this.ifperson = false
				// 	this.iforder = false
				// 	this.ifshelf = false
				// 	this.iforder = true
				// 	this.order_id = this.searchcontent
				// }
				operationRecords(params).then(res =>{
					if (res.result) {
						if(res.mate_info){
							this.mate_info = res.mate_info
							this.record_info = res.record_info
						} else {
							this.record_info = res.record_info
						}
					} else {
						this.$message({
							showClose:true,
							message:res.msg,
							type:'error'
						})
					}
				})
			},
			formatterCode(value){
				let code = value.op_code
				switch(code) {
					case 1:
						return '条码枪上料'
						break
					case 2:
						return '条码枪取料'
						break
					case 3:
						return '异常取料'
						break
					case 4:
						return '重复上架弹出'
						break
					case 5:
						return '禁用物料'
						break
					case 6:
						return 'API上料'
						break
					case 7:
						return 'API取料'
						break
					case 8:
						return '撤消料架亮灯'
						break
					case 9:
						return '工单取料，首套'
						break
					case 10:
						return '工单取料，全套'
						break
					case 11:
						return '站位顺序发料'
						break
					case 12:
						return '库位整理'
						break
					default:
					return '物料转移上架'
				}
			},
			timeFormatter(value){
				return timeConvert(value.in_time,1)
			}
		}
	}
</script>
<style>
	.operation_header{
		display: flex;
		padding:20px 10px;
	}
	.operation_header .el-input{
		width:200px;
		height: 40px;
	}
	.operation_header .el-input__inner{
		width:200px;
		height: 40px;
	}
	.operation_btn .el-button{
		margin-left: 20px;
		height: 40px;
	}
	.operation_select{
		margin-right: 20px;
	}
	.operation_picker{
		margin-left: 20px;
	}
	.operation_picker .el-input__inner{
		width:240px;
	}
	.labeltable {
  border-collapse: collapse;
  margin: 0 auto;
  text-align: center;
  width:100%;
  background: #fff;
}
.caption{
	border:1px solid lightgray;
	border-bottom: none;
	height: 36px;
	line-height: 36px;
	text-align:left;
	padding-left: 4px;
	background: #fff;
	color: #909399;
		font-weight: bold;
}
.labeltable td{
		border:1px solid lightgray;
		height: 36px;
		line-height: 36px;
		color: #909399;
		font-weight: bold;
	}
	.operation_table .title_td_pc{
		/*width:160px;*/
		width:25%;
	}
	.operation_table .content_td_pc{
		/*width:220px;*/
		width:25%
	}
	.order_td{
		width:10%;
	}
	.order_content_td{
		width:15%;
	}
	.opname{
		width:100%;
		height: 36px;
		line-height: 36px;
		text-align: left;
		padding-left: 8px;
		border:1px solid #EBEEF5;
		border-bottom: none;
		background: #fff;
	}
</style>


